const epsilonControl = document.getElementById('epsilon');
const minClusterSizeControl = document.getElementById('min-cluster-size');
const coordWeightControl = document.getElementById('coord-weight');
const constraintStrengthControl = document.getElementById('constraint-strength');

raster.on('beforeoperations', function (event) {
  // Check if map is ready before proceeding
  if (!mapReady) {
    console.log('Map not ready yet, skipping clustering operation');
    return; // Skip this operation if map is not ready
  }

  const data = event.data;
  data.epsilon = epsilonControl.value;
  data.minClusterSize = minClusterSizeControl.value;
  data.coordWeight = parseFloat(coordWeightControl.value);
  data.constraintStrength = parseFloat(constraintStrengthControl.value);

  // Pass view data to the raster operation
  const view = map.getView();
  const center = view.getCenter();
  const resolution = view.getResolution();
  const mapSize = map.getSize();

  // Validate view data before proceeding
  if (!center || !resolution || !mapSize || mapSize[0] === 0 || mapSize[1] === 0) {
    console.warn('View data not available, skipping clustering operation');
    return; // Skip this operation if view data is not available
  }

  data.viewData = {
    center: center,
    resolution: resolution,
    mapSize: mapSize
  };

  console.log('View data for clustering:', {
    center: center,
    resolution: resolution,
    mapSize: mapSize
  });

  // Pass serialized constraint data to the raster operation
  try {
    const constraintData = globalConstraintManager ? globalConstraintManager.exportConstraints() : null;
    data.constraintData = constraintData;
  } catch (error) {
    console.warn('Error serializing constraints for raster operation:', error);
    data.constraintData = null;
  }

  // Always render pixels since we're using opacity for visibility
  data.renderPixels = true;
});

// Store the latest boundary coordinates from the operation
let latestBoundaryCoordinates = {};

// Listen for raster operation completion to update vector layer
raster.on('afteroperations', function (event) {
  // Check if we have boundary coordinates in the event data
  if (event.data && event.data.boundaryCoordinates) {
    latestBoundaryCoordinates = event.data.boundaryCoordinates;
    updateBoundaryVectorLayer(latestBoundaryCoordinates);
  } else {
    console.log('No boundary coordinates in event data, clearing vector source');
    latestBoundaryCoordinates = {};
    clusterBoundarySource.clear();
  }
});

