// Update button active states
function updateDrawingButtonStates() {
  if (!globalDrawingManager || !circleToolButton || !lineToolButton || !disableDrawingButton) {
    return; // Exit if elements or manager are not ready
  }

  const drawingState = globalDrawingManager.getDrawingState();

  // Remove active class from all drawing buttons
  circleToolButton.classList.remove('active');
  lineToolButton.classList.remove('active');
  disableDrawingButton.classList.remove('active');

  // Add active class to current tool
  if (drawingState.isEnabled) {
    if (drawingState.currentTool === 'circle') {
      circleToolButton.classList.add('active');
    } else if (drawingState.currentTool === 'line') {
      lineToolButton.classList.add('active');
    }
  } else {
    disableDrawingButton.classList.add('active');
  }
}

// Circle tool button
circleToolButton.addEventListener('click', function () {
  globalDrawingManager.enableCircleDrawing();
  updateDrawingButtonStates();
  console.log('Circle drawing tool activated');
});

// Line tool button
lineToolButton.addEventListener('click', function () {
  globalDrawingManager.enableLineDrawing();
  updateDrawingButtonStates();
  console.log('Line drawing tool activated');
});

// Disable drawing button
disableDrawingButton.addEventListener('click', function () {
  globalDrawingManager.disableAllDrawing();
  updateDrawingButtonStates();
  console.log('Drawing tools disabled');
});

// Clear constraints button
clearConstraintsButton.addEventListener('click', function () {
  const stats = globalConstraintManager.getStatistics();
  const totalConstraints = stats.mustLinkConstraints + stats.cannotLinkConstraints;

  if (totalConstraints === 0) {
    console.log('No constraints to clear');
    return;
  }

  globalDrawingManager.clearAllAnnotations();
  globalConstraintManager.clearAllConstraints();
  updateDrawingButtonStates();
  updateConstraintTooltip();
  console.log(`Cleared ${totalConstraints} constraints and annotations`);
});

// Update constraint button tooltip with current count
function updateConstraintTooltip() {
  // Safety check - only proceed if both the button and constraint manager exist
  if (!clearConstraintsButton || !globalConstraintManager) {
    console.log('updateConstraintTooltip: DOM elements or constraint manager not ready yet');
    return;
  }

  const stats = globalConstraintManager.getStatistics();
  const totalConstraints = stats.mustLinkConstraints + stats.cannotLinkConstraints;

  if (totalConstraints === 0) {
    clearConstraintsButton.title = 'Clear all constraints and annotations (none currently)';
  } else {
    clearConstraintsButton.title = `Clear all constraints and annotations (${totalConstraints} current: ${stats.mustLinkConstraints} must-link, ${stats.cannotLinkConstraints} cannot-link)`;
  }
}

