// --- Search and Coordinate Functionality ---

// Get DOM elements
const searchInput = document.getElementById('search-input');

// Search history management
const SEARCH_HISTORY_KEY = 'fbd_search_history';
const MAX_HISTORY_ITEMS = 10;

// Function to get search history from localStorage
function getSearchHistory() {
  try {
    const history = localStorage.getItem(SEARCH_HISTORY_KEY);
    return history ? JSON.parse(history) : [];
  } catch (error) {
    console.warn('Error reading search history from localStorage:', error);
    return [];
  }
}

// Function to save search history to localStorage
function saveSearchHistory(history) {
  try {
    localStorage.setItem(SEARCH_HISTORY_KEY, JSON.stringify(history));
  } catch (error) {
    console.warn('Error saving search history to localStorage:', error);
  }
}

// Function to add a new search to history
function addToSearchHistory(lat, lon) {
  const history = getSearchHistory();
  const searchString = `${lat.toFixed(6)} ${lon.toFixed(6)}`;

  // Remove if already exists (to move to top)
  const filteredHistory = history.filter(item => item.coordinates !== searchString);

  // Add to beginning of array
  filteredHistory.unshift({
    coordinates: searchString,
    lat: lat,
    lon: lon,
    timestamp: Date.now()
  });

  // Keep only the most recent searches
  const trimmedHistory = filteredHistory.slice(0, MAX_HISTORY_ITEMS);

  saveSearchHistory(trimmedHistory);
  return trimmedHistory;
}

// Function to create and show search suggestions dropdown
function showSearchSuggestions() {
  const history = getSearchHistory();

  if (history.length === 0) {
    // Show a message when no history exists
    hideSearchSuggestions();

    const dropdown = document.createElement('div');
    dropdown.id = 'search-suggestions';
    dropdown.className = 'search-suggestions';

    const noHistoryMessage = document.createElement('div');
    noHistoryMessage.className = 'search-suggestion-item';
    noHistoryMessage.textContent = 'No search history yet';
    noHistoryMessage.style.color = '#6c757d';
    noHistoryMessage.style.fontStyle = 'italic';
    noHistoryMessage.style.cursor = 'default';

    dropdown.appendChild(noHistoryMessage);

    const searchContainer = document.querySelector('.search-container');
    searchContainer.appendChild(dropdown);

    dropdown.style.position = 'absolute';
    dropdown.style.top = '100%';
    dropdown.style.left = '0';
    dropdown.style.right = '0';
    dropdown.style.zIndex = '1000';

    return;
  }

  // Remove existing dropdown if present
  hideSearchSuggestions();

  // Create dropdown container
  const dropdown = document.createElement('div');
  dropdown.id = 'search-suggestions';
  dropdown.className = 'search-suggestions';

  // Add suggestions
  history.forEach((item) => {
    const suggestion = document.createElement('div');
    suggestion.className = 'search-suggestion-item';
    suggestion.textContent = item.coordinates;
    suggestion.dataset.lat = item.lat;
    suggestion.dataset.lon = item.lon;

    // Add click handler
    suggestion.addEventListener('click', function() {
      const lat = parseFloat(this.dataset.lat);
      const lon = parseFloat(this.dataset.lon);
      navigateToCoordinates(lat, lon);
      hideSearchSuggestions();
    });

    // Add hover effect
    suggestion.addEventListener('mouseenter', function() {
      this.style.backgroundColor = '#f0f0f0';
    });

    suggestion.addEventListener('mouseleave', function() {
      this.style.backgroundColor = '';
    });

    dropdown.appendChild(suggestion);
  });

    // Position dropdown below search input
  const searchContainer = document.querySelector('.search-container');
  searchContainer.appendChild(dropdown);

  // Position dropdown correctly - account for flexbox layout
  dropdown.style.position = 'absolute';
  dropdown.style.top = '100%';
  dropdown.style.left = '0';
  dropdown.style.right = '0';
  dropdown.style.zIndex = '1000';
}

// Function to hide search suggestions dropdown
function hideSearchSuggestions() {
  const existingDropdown = document.getElementById('search-suggestions');
  if (existingDropdown) {
    existingDropdown.remove();
  }
}

// Function to clear search history
function clearSearchHistory() {
  try {
    localStorage.removeItem(SEARCH_HISTORY_KEY);
    hideSearchSuggestions();
    console.log('Search history cleared');
  } catch (error) {
    console.warn('Error clearing search history:', error);
  }
}

// Function to validate coordinates
function validateCoordinates(lat, lon) {
  const latNum = parseFloat(lat);
  const lonNum = parseFloat(lon);

  if (isNaN(latNum) || isNaN(lonNum)) {
    alert('Please enter valid numeric coordinates separated by space (e.g., 55.576662 13.283111)');
    return false;
  }

  if (latNum < -90 || latNum > 90) {
    alert('Latitude must be between -90 and 90 degrees.');
    return false;
  }

  if (lonNum < -180 || lonNum > 180) {
    alert('Longitude must be between -180 and 180 degrees.');
    return false;
  }

  return { lat: latNum, lon: lonNum };
}

// Function to navigate to coordinates
function navigateToCoordinates(lat, lon) {
  const view = map.getView();
  const coordinates = fromLonLat([lon, lat]);

  // Animate to the new location
  view.animate({
    center: coordinates,
    duration: 1000,
    zoom: 15
  });

  // Update search input with formatted coordinates
  searchInput.value = `${lat.toFixed(6)} ${lon.toFixed(6)}`;

  // Add to search history
  addToSearchHistory(lat, lon);
}

// Function to parse and handle coordinate input
function handleCoordinateInput() {
  const input = searchInput.value.trim();

  if (!input) {
    return; // Empty input, do nothing
  }

  // Split by space and filter out empty strings
  const parts = input.split(/[\s,]+/).filter(part => part.length > 0);

  if (parts.length !== 2) {
    alert('Please enter exactly two coordinates separated by space (latitude longitude)');
    return;
  }

  const [latStr, lonStr] = parts;
  const validatedCoords = validateCoordinates(latStr, lonStr);

  if (validatedCoords) {
    navigateToCoordinates(validatedCoords.lat, validatedCoords.lon);
  }
}

// Event listeners for search functionality
searchInput.addEventListener('keypress', function(event) {
  if (event.key === 'Enter') {
    handleCoordinateInput();
    hideSearchSuggestions();
  }
});

// Show suggestions when input is focused
searchInput.addEventListener('focus', function() {
  showSearchSuggestions();
});

// Hide suggestions when clicking outside
document.addEventListener('click', function(event) {
  const searchContainer = document.querySelector('.search-container');
  const suggestions = document.getElementById('search-suggestions');

  if (!searchContainer.contains(event.target) && suggestions) {
    hideSearchSuggestions();
  }
});

// Hide suggestions when pressing Escape
document.addEventListener('keydown', function(event) {
  if (event.key === 'Escape') {
    // Only hide suggestions if drawing is not enabled
    if (!globalDrawingManager || !globalDrawingManager.isDrawingEnabled) {
      hideSearchSuggestions();
    }
  }
});

// Clear history button functionality
const clearHistoryButton = document.getElementById('clear-history');
clearHistoryButton.addEventListener('click', function(event) {
  event.stopPropagation(); // Prevent triggering the document click event
  clearSearchHistory();
});

